import { GB } from 'constants/countries';
import { fallbackMap } from 'constants/languages';
import { Language, LanguageScheme, ShortLanguage, ShortLanguageScheme, } from 'types/common';
export var getSupportedLanguage = function (language, fallbackCountryCode) {
    if (LanguageScheme.guard(language)) {
        return language;
    }
    if (ShortLanguageScheme.guard(language)) {
        switch (language) {
            case ShortLanguage.EN:
                return fallbackCountryCode === GB ? Language.EN_GB : Language.EN_US;
            default:
                return fallbackMap[language];
        }
    }
    return Language.EN_US;
};
